.header {
overflow:hidden;
position:relative;
width: 100%;
//min-height:38.5vh;
//height:22rem;
background-repeat:no-repeat;
background-position: center;
background-size: cover ;

// header media queries
@include responsive(small-phone){
height:22rem;
}//575px
@include responsive(phone-500){
height:19rem;
}//411px
@include responsive(pixel-2){
height:18.5rem;
}//411px
@include responsive(iphone-x){
height:18rem;
}//376px
@include responsive(galaxy){
height:18rem;
}//361px
@include responsive(iphone-5){
height:17rem;
}//321px
@include responsive(galaxy-fold){
height:17rem;

}//280px

@include responsive(phone){
height:23.5rem;
}//end phone


@include responsive(tablet-min){
height:26rem;
}//574px x 1.13 = 650px

@include responsive(tablet){
height:28rem;
}//end tablet
@include responsive(big-tablet){

}//end big-tablet

@include responsive(laptop){
height:33rem;
}//end big-tablet

@include responsive(desktop){
//height:60vh;
height:40rem;
}//end desktop
@include responsive(big-desktop){
height:43rem;
}//end big-desktop
@include responsive(developer){
//height:65vh;
height: 52rem;
}//end developer
@include responsive(tv){
//height:65vh;
height: 57rem;
}//end developer
@include responsive(mid-tv){
//height:65vh;
height: 64rem;
}//end developer
@include responsive(big-tv){
//height:65vh;
height: 71rem;
}//end developer

& > .hero-text{
grid-column: 5 / 22;
grid-row: 3 / 6;
height:100%;
width:100%;
display:flex;
align-content:center;
justify-content:center;   
flex-wrap:wrap;
text-align:center;

@include background(none, $color-dark, 0.2, cover);
border-radius:10px;

@include responsive(phone){
grid-row: 2 / 6;
}//1030px


@include responsive(laptop){
grid-column: 8 / 21;
}//1030px

& > h2 {
//@include text-template($supersized, $montserrat, $color-primary );
font-family:$montserrat;
color:$color-primary;
font-style: italic; 
font-weight: 800;
font-size:$normal;

@include responsive(galaxy-fold){}//280px
@include responsive(iphone-5){}//321px
@include responsive(galaxy){}//361px
@include responsive(iphone-x){}//376px
@include responsive(pixel-2){}//411px
@include responsive(small-phone){}//575px
//forward cascade
@include responsive(phone){font-size: $med;}//574px
@include responsive(tablet-max){}//769px
@include responsive(tablet){}//768px
@include responsive(big-tablet){}//992px
@include responsive(laptop){
font-size:$larger;
}//1030px
@include responsive(laptop-max){}//1031px
@include responsive(desktop)
{font-size:$jumbo;
}//1200px
@include responsive(big-desktop){}//1400px
@include responsive(developer){
font-size:$massive;
}//1600px
@include responsive(developer-plus){}//1800px
@include responsive(tv){
font-size:$xlarge;
}//2000px
@include responsive(mid-tv){
font-size:$supersized;
}//2250px
@include responsive(big-tv){
font-size:$gargantuan;
}//2500px
}//end h2
}//end hero text



}//end header
