.main{
width:100%;
height:min-content;
min-height:100vh;
display:flex;
flex-wrap:wrap; 
align-content:flex-start;
justify-content:flex-start; 
margin:0px;
padding:1.5rem 0rem 0rem 0rem;
overflow:hidden;

& > .banner-title{
width:100%;
height:100%;
display:flex;
justify-content:center;
align-content:center;

}//.banner-title

& > .posts{
display:flex;
flex-wrap:wrap; 
align-content:flex-start;
justify-content:flex-center;
flex-direction: row; 
margin:0px;
height:100%;
width:100%;
padding: 1.5rem 0rem 1rem 0rem;

& > div{
width:100%;
display:flex;
align-content:center;
justify-content:center;
flex-wrap:wrap;
flex-direction: row;

& > ul{
display:flex;
flex-direction: row;
flex-wrap:wrap;
margin-bottom:20px;

& > .page-item.active > .page-link {
z-index: 1;
color: #fff;
color: $color-preview-tags-text;
background-color: $color-preview-tags-background;
border-color: transparent;
font-family:$tags-text;
}

& > .page-item.disabled > .page-link {
color:$color-preview-tags-text;
pointer-events: none;
cursor: auto;
background-color: #fff;
border-color: #dee2e6;
font-family:$tags-text;
}

& > li{
& > .page-link {
color:$color-preview-tags-text;
font-family:$tags-text;
}//page-link

}//end li
}//end ul
}//end div

}//end posts

}//end main
