body {
display:grid;
grid-template-columns: 1fr;
grid-template-rows:auto auto auto;
background: $color-body;
@include responsive(big-tv){
grid-template-columns: 1fr minmax(auto, 2500px) 1fr;
}

& > .navigation {
grid-column: 1 / -1;
@include responsive(phone){
//grid-row: 1;
}
@include responsive(tablet){
grid-row: 1;
}
}//end navigation

& > .footer {
grid-column:1 / -1;
grid-row:3;

@include responsive(tablet) {
grid-row:3;
}
}//end footer

} // end body

.wrapper {
grid-column:1 / -1;
grid-row:2;
display:grid;
width:100%;
margin-top:4.7025rem;
grid-template-columns: 8px repeat(12, 1fr 8px);  
grid-template-rows:auto;
background:$color-wrapper-background;

//forward cascade
@include responsive(phone){
grid-template-columns: 16px repeat(12, 1fr 16px);
}
@include responsive(tablet){
margin-top:0rem;
grid-template-rows:auto;
}
@include responsive(big-tablet){
grid-template-columns: 16px repeat(12, 1fr 16px);
}
@include responsive(desktop){
grid-template-rows:auto;
}
@include responsive(big-tv){
grid-column:2;
}

& > .header {
grid-column: 1 / 26;
grid-row:  1 / 2 ;
display:grid;
grid-template-columns: 24px repeat(12, 1fr 24px);  
grid-template-rows: repeat(6, 1fr);
}//end header

& > .main {
grid-column: 2 / 25 ;
grid-row: 2 / 3;

@include responsive(phone) {
grid-column: 3 / 24 ;
}

@include responsive(tablet) {
grid-column: 4 / 23 ;//cascade 
grid-row: 2 / 3;//cascade
}

@include responsive(big-tablet) {
grid-column: 5 / 22 ;//cascade 
}

@include responsive(laptop) {
grid-column: 6 / 21 ;//cascade 
}

@include responsive(desktop) {
grid-column: 6 / 21 ;//cascade
grid-row: 2 / 3; //cascade
}

@include responsive(big-desktop) {
grid-column: 7 / 20 ;//cascade
}

@include responsive(developer) {
grid-column: 7 / 20 ;
}

@include responsive(developer-plus-two) {
grid-column: 8 / 19 ;
}

}//end main

} // end wrapper
