$min_width: 280px;
$max_width: 2500px;
$min_font: 12px;
$max_font: 26px;

html {
box-sizing: border-box;
font-size: 16px;
line-height: 1.5;
display:block;

@include fluid-type($min_width, $max_width, $min_font, $max_font);
} // end html
body {
display:grid;
grid-template-columns: 1fr;
grid-template-rows:auto auto auto;
font-family: $font-family-reset;
color: $color-text;
font-weight: 400;
line-height: 1.5;
min-height:100vh;
//background-image: linear-gradient(180deg, rgba($mogb, .3), rgba($mogb, .1)), url();
background-color: $color-primary;
$mogb:#4C5D73;
$vligb:#90B0D9 ;
$gwh:#ECEDEC ;
overflow-x:hidden;
box-sizing:border-box;

@include responsive(big-tv){
grid-template-columns: 1fr minmax(auto, 2500px) 1fr;
}

} // end body
.wrapper {
grid-column:1 / -1;
grid-row:1;
display:grid;
width:100%;
height:auto;
overflow:hidden;
grid-template-columns: repeat(12, [col-start] 1fr);
grid-template-rows:auto;

@include responsive(big-tv){
grid-column:2;
}

} // end wrapper
.sticky {
//position: fixed;
position: sticky;
//left: 0;
//right: 0;
top: 0;
z-index: 10;
}//end sticky
#bh-form{
& > .form-group{
margin-bottom: .8rem;
display:flex;
flex-wrap:wrap;
& > .form-control {
display: block;
width: 100%;
height:auto;
padding: .375rem .75rem;
background-color: #fff;
background-clip: padding-box;
border: 1px solid #ced4da;
border-radius: .25rem;
line-height: 1.5;
@include text-ph($font-family-base, $normal, 400, $color-form-control);
transition: border-color .15s ease-in-out,box-shadow .15s ease-in-out;
}
& > .bh-btn {
color: #fff;
background-color:$bh-button-color; 
border: 1px solid transparent;
border-color: $bh-button-color;
width: auto;
@include padding-r(1rem, 1.4rem, .8rem, 1.4rem);
margin: 1.2rem 0rem 1.2rem 0rem;
border-radius: 4px 4px 4px 4px;
@include text-ph($bh-button-font, $normal-plus, 400, $color-preview-tags-text);

text-align:center;
transition: all .2s ease-in-out;
line-height: 1.5;
user-select:none;
vertical-align: middle;
display: inline-block;

@include responsive(laptop){
@include padding-r(.5rem, .7rem, .4rem, .7rem);
}//575px

}//end bh-btn
& > .bh-btn:hover{
transform: scale(1.1);
}
& > input{
margin-top:0px;
//margin-top:20px;
//font-size: ;
@include responsive(small-phone){}//575px
@include responsive(phone-500){}//411px
@include responsive(pixel-2){}//411px
@include responsive(iphone-x){}//376px
@include responsive(galaxy){}//361px
@include responsive(iphone-5){}//321px
@include responsive(galaxy-fold){}//280px
@include responsive(phone){}//575px
}//end input
}//end .form-group
}//end bh-form
.blog-heading{
padding:0rem;
margin-bottom:.5rem;
width:100%;
text-align:center;

@include text-ph($title-blog, $massive, 600, $blog-headlines);

@include responsive(desktop){
margin-bottom:1rem;
}//end desktop

@include responsive(tv){
margin-bottom:1.3rem;
}//end developer
}//end .blog-heading
.p-news{
width:100%;
height:auto;
@include text-ph($font-family-arimo, $jumbo, 400, $color-p-news);
}//end p-news
#disqus_thread{
width: 100%;
max-width: 800px;
//reverse cascade
@include responsive(small-phone){
margin-top: 2rem;
}//500px x 1.15 = 574px
//forward cascade
@include responsive(tablet){
margin-top: 3rem;
}//650px x 1.18 = 768px
@include responsive(desktop){
margin-top: 4rem;
}//1030px x 1.16 = 1200px
@include responsive(developer){
margin-top: 4.5rem;
}//1400px x 1.142 = 1600px
@include responsive(tv){
margin-top: 5rem;
}//1920px x 1.041 = 2000px

& > iframe > iframe:nth-child(1) {
height:none !important;  
display:none !important;
}//disqus ads

& > iframe:nth-child(3){
height:none !important;  
display:none !important;
}

}//disqus ads
.feature-no-image{
display:none;
}
.aria-invisible {
border: 0; 
clip: rect(0 0 0 0); 
height: 1px;  
margin: -1px;
overflow: hidden;
padding: 0;
position: absolute;
width: 1px;
}
.form-text{
margin:.4rem 0rem .8rem .8rem;
@include text-ph($font-family-base, $small, 400, $color-form-control);
//$color-p-news
}
.home-btn {
color: #fff;
background-color:$bh-button-color; 
border: 1px solid transparent;
border-color: $bh-button-color;
width: auto;
//padding: .5rem .7rem .4rem .7rem;
@include padding-r(1rem, 1.4rem, .8rem, 1.4rem);

margin: 1.2rem 0rem 1.2rem 0rem;
border-radius: 4px 4px 4px 4px;
@include text-ph($bh-button-font, $normal-plus, 400, $color-preview-tags-text);

text-align:center;
transition: all .2s ease-in-out;
line-height: 1.5;
user-select:none;
vertical-align: middle;
display: inline-block;

@include responsive(laptop){
@include padding-r(.5rem, .7rem, .4rem, .7rem);
}//575px

}//end bh-btn
.home-btn:hover{
transform: scale(1.1);
}
.page-link{
padding: .45rem 1rem;
}
