//animations, positions are initially set relative absolute positioned parent, element then transformed initially
//on javascript enable visibility, transformation is removed and elements return to initial positions
@mixin transition{
transition: all .2s ease-in-out;
}
//logo-box
@mixin logo-box{
transition: transform 1.5s  cubic-bezier(0,1,.3,1);
}
//banner-box (set equal logo-box)
@mixin header-box-header{
//transition: transform 1s cubic-bezier(0.68, -0.6, 0.32, 1.6);
transition: transform 1.5s  cubic-bezier(0,1,.3,1);
}
@mixin banner-box-banner{
transition: transform 1s cubic-bezier(0.68, -0.6, 0.32, 1.6);
//transition: transform 1.5s  cubic-bezier(0,1,.3,1);
}
//logo-tags
@mixin logo-tags{
transition: transform 1.5s  cubic-bezier(0,1,.3,1);
}
//logo-tags-last
@mixin logo-tags-last{
//animation: logo-tags-last-a 1.5s cubic-bezier(0, 0.5, 0, 1) both;
animation: logo-tags-last-a 1.5s cubic-bezier(0,1,.3,1) both;
}

