}
//header-banner-header
@mixin banner-header{
transition: all 0.5s ease-in-out;
}
//header-banner-checklist
@mixin banner-transition{
transition: all .2s ease-in-out;
}

// ball horizontal
@mixin ball-horizontal{
//animation: pops 1s ease-in-out forwards;
//animation: ball-horizontal 1.5s cubic-bezier(.64,1.06,1,.58) forwards;
//animation: ball-horizontal 1.7s cubic-bezier(.37,.55,.49,.67) forwards;
animation: ball-horizontal 1.7s cubic-bezier(.37,.55,.49,.67) forwards;
}

//left-right movement of ball container
@keyframes ball-horizontal {
0%{
opacity:1;
transform: translateX(0vw);
}//end 0
25%{
//opacity:0;
}
50%{
}//
60%{
//transform: translateX(75vw);
}//
100%{
transform: translateX(77vw);
opacity:1;
}
}//end ball-horizontal

//ball vertical
@mixin ball-vertical {
//animation: pop-container 1s ease-in-out forwards;
//animation: pop-container 1s ease-out-in forwards;
//animation: pop-container .6s cubic-bezier(.33,.99,.64,.03) forwards;
//animation: ball-vertical 1.5s cubic-bezier(.64,1.06,1,.58) forwards;
animation: ball-vertical 1.7s linear forwards;
}

//movement of container with popped image and pink bubble
@keyframes ball-vertical {
0%{
opacity:1;
//transform:translateY(-8em);
transform:translateY(-20vh);
animation-timing-function: cubic-bezier(.51,.01,.79,.02);
}//end 0
33%{
width:12em;
}
50%{
//transform: translateY(20em);
width:14em;
height:12em;
transform:translateY(85vh);
animation-timing-function: cubic-bezier(.19,1,.7,1);
}
66%{
height:13em;
width:13em;
}
75%{
//transform: translateY(-4em);
}
100%{
//transform: translateY(-4em);
transform:translateY(0vh);
opacity:1;
}
}//end ball-vertical

//header-banner-bubble
@mixin the-ball{
animation: the-ball 1.5s ease both;
//animation: the-ball .8s cubic-bezier(.64,1.06,1,.58) forwards;
}

@keyframes the-ball {
0%{
opacity:0;
background:$color-hero-pops;
color:transparent;
//color:transparent;
}//end 0
3%{
opacity:1;
}
50%{
}
75%{
//transform:scale(1);
}

90%{
//transform:scale(1);
color:transparent;
}

100%{
transform: rotatez(12deg);
opacity:1;
//background:transparent;
color:$color-primary;
box-shadow:none;
}
}//end pops

@mixin popped-image{
//animation: pops 1s ease-in-out forwards;
//animation: pops 1s ease-out-in forwards;
//animation: pops .6s cubic-bezier(.33,.99,.64,.03) forwards;
animation: popped-image .8s cubic-bezier(.64,1.06,1,.58) forwards;
}

@keyframes popped-image {
0%{
opacity:0;
transform:scale(0);
}//end 0
25%{
opacity:0;
}
50%{
}
95%{
opacity:0;
transform:scale(0);
}
100%{
opacity:1;
transform:scale(1.1);
}
}//end pops

