//animation/transition delays
//tags
$tag-1:.3s;
$tag-2:.4s;
$tag-3:.5s;
$tag-last:1s;
//immediate
$zero-delay:0s;

$horizontal-ball:3.8s;
$vertical-ball:3.8s;
$the-ball:0s;

$popped-image:1.52s;
//header-banner header delays
$banner-header:1.2s;
$header-banner-ph: 1s;
$header-banner-p1: 2s;
$header-banner-p2: 3s;
$header-banner-p3: 4s;
$header-banner-ch: 2.6s;
$header-banner-c1: 3.6s;
$header-banner-c2: 4.6s;
$header-banner-c3: 6.6s;
$header-banner-c4: 7.6s;
$header-banner-c5: 8.6s;
//pops fires after c2
$header-banner-pops: 5.4s;
//call number fires after c5
$header-banner-phone-no: 9.5s;
// phone-image
$header-banner-phone-img: 10.5s;
