.navigation {
grid-column: 1 / -1;
grid-row: 1 / 3;
align-self:start;
display:grid;
grid-template-columns:1fr; 
grid-template-rows:1fr;                
//min-height:7vh;
min-height:4.7025rem;
width:100%;
height:auto;
padding:0px;
background:$navigation-background;
align-items:center;
overflow:hidden;
z-index: 10;
position: sticky;
top: 0;


@include responsive(tablet){
//min-height:4.7025rem;
}

@include responsive(big-tv){
grid-template-columns:1fr minmax(auto, 2500px) 1fr;
}//2500px

& > .navigation_container{
grid-column:1 / -1; 
grid-row:1; 
display:grid;
grid-template-columns:1fr 1fr 8fr;
//grid-template-rows:auto;
grid-template-rows:minmax(4.7025rem, auto) auto;
height:100%;
align-items: center;
background:$navigation-background;
z-index:1;

@include responsive(tablet){
grid-row: 1;
}

@include responsive(tablet){
grid-template-columns:1fr 8fr 1.5fr;
}

@include responsive(big-tv){
grid-column: 2;
}//2500px

& > a.brand{
grid-column:1;
grid-row:1;
display:inline-block;
//width:80px;
//height:40px;
height:65%;
//width:auto;

margin: 0px 10px 0px 10px;

@include responsive(phone){
//height:40px;
//width:80px;
}

& > .footer-logo{
border-radius:$header-b-rad;
height:100%;
width:100%;

& > .img {
width:100%;
height:100%;
object-fit:contain;
object-position:center center;
border-radius:$rad-normal;
display:flex;
transition: all .2s ease-in-out;
align-items:center;
justify-content:center;
}//end image

}//end footer logo picture tag

}//end develop link <a>

& > a.brand:hover{
transform: scale(1.2);
}

& > .hamburger{
grid-column:2;
grid-row:1;

justify-self:center;
display:flex;
align-items:center;
justify-content:center;
background:transparent;

border:none;
//border: 1px solid hsla(0,0%,100%,.1);
//border-radius: .25rem;

padding: .25rem .75rem;
color: white;

& > svg{
align-self:center;
height:2.8rem;
width:auto;
//color:$color-primary;
}//hamburger icon


@include responsive(tablet){
display:none;
}
}//end hamburger

& > hamburger:focus, & > button:focus{
outline:none;
}

& > .is-active{
border: 1px solid #fff;
border-radius:$header-b-rad;
}

& > .main-nav{
grid-column:1 / 4;
grid-row:2/3;
display:none;
justify-self:start;
align-self:center;
width:100%;
margin:0px;
height:auto;

@include responsive(tablet){
grid-column:2;
grid-row:1;
justify-self:start;
display:flex;
flex-direction:row;
background-color:transparent;
}

& > ul{
display:flex;
flex-direction:column;
flex-wrap:wrap;
margin:0px;
list-style-type:none;
height:auto;
justify-content:space-evenly;

@include responsive(tablet){
flex-direction:row;
align-items: center;
justify-content:center;
}

//this is .nav-item
& > li{
margin:.5rem 0rem;

@include responsive(small-phone){
margin:.5rem 0rem;
}

@include responsive(tablet){
margin:0rem 0rem;
}

& > .nav-link {
color:$color-primary;
font-weight:bold;

//font-family:$font-family-ads;
font-family:$font-family-nordique;
padding: .5rem 1rem .5rem 0rem;

@include responsive(small-phone){
font-size:$large;
}

@include responsive(phone){
font-size:$big-plus;
}

@include responsive(tablet){
padding: 0rem 1rem 0rem 0rem;
}

@include responsive(desktop){
font-size:$med;
}
@include responsive(big-desktop){
font-size:$large;
}

@include responsive(tv){
font-size: $larger;
}

}//end nav-link

}//end li
}//end ul

}//end main nav

& > .drop-down{
display:flex;
}

& > .phone{
grid-column:3;
grid-row:1;
text-align:right;
align-self:center;
justify-self:end;
width:95%;
display:flex;
justify-content:flex-end;

& > .nav-link {
color:$color-primary;
font-weight:bold;
font-family:$font-family-nordique;
padding: .5rem 1rem;

@include responsive(small-phone){
font-size:$large;
}

@include responsive(phone){
font-size:$big-plus;
}

@include responsive(tablet){
font-size:$large;
padding: 0rem 1rem 0rem 0rem;
}

@include responsive(tv){
font-size: $larger;
}
}//end nav-link


& > a{
align-self:center;
}
}//phone



.navbar-custom {
background-color: rgba(0,0,0, 0.9);
min-height:7vh;
width:100%;
}

// for local hrefs
a.anchor {
display: block;
position: relative;
top: 25px;
visibility: hidden;
}//end anchor
}//end navigation container
}//end navigation
