.preview_loops, .post-search-grid {
display: grid;
grid-template-columns: 1fr;
grid-template-rows: 9.5rem .1fr minmax(5rem, auto);
border: 1px solid #ccc;
border-radius: $rad-big;
justify-content: center;
 
@include responsive(small-phone){
grid-template-rows: 9.5rem .1fr minmax(5rem, auto);
margin:0px 0px 30px 0px;
margin:0rem 0rem 1.875rem 0rem;
}//575px

@include responsive(phone){
margin:0rem 0rem 2.5rem 0rem;
}
@include responsive(tablet){
grid-template-rows: 7rem .1fr minmax(5rem, auto);
}
@include responsive(big-tablet){
}
@include responsive(desktop){
grid-template-rows: 7.5rem .1fr minmax(5rem, auto);
margin:0rem 0rem 3.125rem 0rem;
}//end desktop
@include responsive(big-desktop){
grid-template-rows: 8rem .1fr minmax(5rem, auto);
}
@include responsive(developer){
grid-template-rows: 8.5rem .1fr minmax(5rem, auto);
}
@include responsive(tv){
grid-template-rows: 9rem .1fr minmax(5rem, auto);
}

& > .side-preview-image {
grid-column:1;
grid-row:1;
height:100%;
width:auto;
display:flex;
background-size: cover;
background-repeat: no-repeat;
background-position: center;
border-radius:$top-rad-big;
align-items:center;
justify-content:center;
margin:0px;

& > .img {
width:100%;
height:100%;
object-fit:cover;
object-position:center center;
border-radius:$top-rad-big;
}//end image

}//end .side-preview-image

& > h3 {
height:100%;
width:100%;
grid-column:1;
grid-row:3;
@include text-ph($previews-blog,$massive, 600, $blog-headlines);
justify-self:center;
align-self:center;
text-align:center;

@include responsive(small-phone){
padding: .3rem .5rem 1rem .5rem;
}//575px
@include responsive(galaxy){
padding: .3rem .3rem 1rem .3rem;
}//361px
@include responsive(phone){
padding: .6rem .6rem 1rem .6rem;
}//280px

@include responsive(desktop) {
padding: 1rem 1rem 1.3rem 1rem;
}

@include responsive(developer) {
align-self:start;
padding:1.2rem 1.2rem 1.5rem 1.2rem;
}

@include responsive(tv){
padding:1.3rem 1.3rem 1.6rem 1.3rem;
}//1920px x 1.041 = 2000px

}//end h3

& > p {
grid-column:1;
grid-row:2;
justify-self:start;
align-self:flex-start;
padding: .8rem;
text-direction: none; 
@include text-ph( $font-family-base, $big, 400, $color-preview-date)

@include responsive(tablet){
//@include text-r( $font-family-base, $normal, 400, $color-preview-date)
}//768px
}//end date

}//end recent link (close loop)

.heroText{
grid-column: 5 / 22;
grid-row: 3 / 6;
height:100%;
width:100%;
display:flex;
align-content:center;
justify-content:center;   
flex-wrap:wrap;
text-align:center;
border-radius:10px;
padding: 0.2rem 0.5rem;

@include responsive(galaxy-fold){
grid-column: 5 / 21;
}//1030px

@include responsive(phone){
grid-row: 2 / 6;
}//1030px


@include responsive(laptop){
grid-column: 8 / 19;
padding: 1rem 1rem  1rem 1rem;
}//1030px

& > h2{
font-family:$montserrat;
color:$color-primary;
font-style: italic; 
font-weight: 800;


@include responsive(small-phone){
font-size: $less-big;
}//575px

@include responsive(galaxy){
font-size: $normal;}//361px

@include responsive(iphone-5){
font-size: $normal;
}//321px

@include responsive(galaxy-fold){
font-size: $small;
}//280px

//forward cascade
@include responsive(phone){font-size: $med;}//574px
@include responsive(tablet-max){}//769px
@include responsive(tablet){}//768px
@include responsive(big-tablet){}//992px
@include responsive(laptop-max){}//1031px
@include responsive(laptop){
font-size: $large;
}//1030px
@include responsive(desktop)
{font-size:$jumbo;
}//1200px
@include responsive(big-desktop){}//1400px
@include responsive(developer){
font-size:$massive;
}//1600px
@include responsive(developer-plus){
font-size:$xlarge;
}//1800px
@include responsive(tv){
font-size:$supersized;
}//2000px
@include responsive(mid-tv){
font-size:$gargantuan;
}//2250px
@include responsive(big-tv){
font-size:$gargantuan;
}//2500px
}//end h2
}//heroText

